/* Set the font family and size */
body {
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f7f7f7;
  text-align: center; /* Add this line to center align text */
  margin-top: 60px;
}

/* Set the max width of the website */
.container {
  max-width: 800px;
  padding: 0 20px;
}

/* Style the header */
header {
  text-align: center;
  margin-top: 30px;
}

header h1 {
  font-size: 36px;
  color: #333;
}

/* Style the sections */
section {
  margin-top: 50px;
  border-top: 1px solid #ddd;
  padding-top: 20px;
}

section h2 {
  font-size: 24px;
  color: #333;
}

section ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

section ul li {
  margin-bottom: 10px;
  color: #666;
}

section p {
  margin-bottom: 10px;
  color: #666;
}

/* Style the links */
a {
  color: #0077ff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

/* Style the lists */
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul li {
  margin-bottom: 10px;
  color: #666;
}

/* Add a more professional tone */
.container, header, section {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #333;
}

header h1 {
  font-size: 48px;
}

section h2 {
  font-size: 36px;
}

ul li {
  font-size: 18px;
}

a {
  color: #0077ff;
  text-decoration: none;
  border-bottom: 2px solid #0077ff;
}

a:hover {
  text-decoration: none;
  border-bottom: 2px solid #0057ad;
}

body {
