}
.about-me-content {
  margin-right: 10%;
  display: flex;
  flex-wrap: nowrap;
  grid-gap: 1rem;
  gap: 1rem;
  text-align: center;
  justify-content: center;
  align-items: flex-start;
}

.portrait {
  max-width: 200px;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-right: 2rem;
}

.about-me-text {
  max-width: 60%;
  padding-bottom: 10%;
}

.about-me-links {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  width: 100%;
}

.contact-links a {
  margin-right: 1rem;
}

main {
  margin-top: 60px;
}

.break {
  flex-basis: 100%;
  height: 0;
}

.space {
  height: 60px;
  margin-top: -60px;
}

#about-me,
#contact,
#projects {
  padding-top: 60px;
}

.contact-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resume-download {
  margin-top: 1rem;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
}

.project {
  padding: 1rem;
}

.video-container {
  position: relative;
  width: 80%;
  padding-bottom: 45%; /* 16:9 aspect ratio */
  margin: 1rem auto;
  overflow: hidden;
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

nav .nav-link {
  font-size: 1.2rem;
  color: #1d1f2f;
}

nav .nav-link:hover {
  color: #c4a7ff;
}

@media (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
  }

  .about-me-links {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .about-me-text {
    max-width: 100%;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  h2 {
    font-size: 3rem;
  }

  nav .nav-link {
    font-size: 1.4rem;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 2.5rem;
  }

  nav .nav-link {
    font-size: 1.3rem
  }
}
.header-container {
  font-family: "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 60px;
  background-color: #333333;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  margin-top: 0;
  -webkit-animation: fadeInDown 1s ease-in-out;
          animation: fadeInDown 1s ease-in-out;
}

@-webkit-keyframes fadeInDown {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInDown {
  0% {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.header-text {
  font-size: 60px;
  color: #ffffff;
  margin-bottom: 0.5rem;
  text-shadow: 2px 2px #333333;
}

.header-nav {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  justify-content: center;
  width: 100%;
  background-color: #444444;
}

.header-nav a {
  font-size: 1.8rem;
  color: #ffffff;
  text-decoration: none;
  padding: 0.5rem 1.5rem;
}

.header-nav a:hover {
  background-color: #555555;
}

form {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 0 auto;
  padding: 2rem;
  border: 2px solid #ccc;
  border-radius: 10px;
}

label {
  margin: 1rem 0;
  font-size: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
  resize: vertical;
}

textarea {
  height: 10rem;
}

button[type="submit"] {
  background-color: #1e90ff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

button[type="submit"]:hover {
  background-color: #0077b6;
}

/* Responsive design */
@media (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
  }

  .portrait {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  h2 {
    font-size: 2.5rem;
  }

  .header-text {
    font-size: 28px;
  }

  nav .nav-link {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 2rem;
  }

  .header-text {
    font-size: 24px;
  }

  nav .nav-link {
    font-size: 0.9rem;
  }
}

