form {
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: 0 auto;
  padding: 2rem;
  border: 2px solid #ccc;
  border-radius: 10px;
}

label {
  margin: 1rem 0;
  font-size: 1.2rem;
}

input,
textarea {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1rem;
  resize: vertical;
}

textarea {
  height: 10rem;
}

button[type="submit"] {
  background-color: #1e90ff;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

button[type="submit"]:hover {
  background-color: #0077b6;
}

/* Responsive design */
@media (max-width: 768px) {
  .about-me-content {
    flex-direction: column;
  }

  .portrait {
    margin-right: 0;
    margin-bottom: 1rem;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  h2 {
    font-size: 2.5rem;
  }

  .header-text {
    font-size: 28px;
  }

  nav .nav-link {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  h2 {
    font-size: 2rem;
  }

  .header-text {
    font-size: 24px;
  }

  nav .nav-link {
    font-size: 0.9rem;
  }
}
